import React, { useState } from 'react';
import logo from './logo.svg';
import './App.css';

// libraries
import { ENS } from '@ensdomains/ensjs';

import { ethers } from 'ethers'
import { Buffer } from 'buffer'

import web3 from 'web3'


import { Formik, useFormik } from 'formik';
import * as Yup from 'yup';
import {
  Button,
  TextArea,
  Spinner,
  Callout,
  Popover,
  AnchorButton,
  Menu,
  MenuItem,
} from '@blueprintjs/core';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Container from 'react-bootstrap/Container';

import '@blueprintjs/core/lib/css/blueprint.css';
import '@blueprintjs/icons/lib/css/blueprint-icons.css';
import 'bootstrap/dist/css/bootstrap-grid.min.css';

const v: any = window;

function ENSForm() {
  const ENSInstance = new ENS();
  window.Buffer = Buffer;
  const provider = new ethers.providers.Web3Provider(v.ethereum);

  const [response, setResponse] = useState<any>(null)
  const [loading, setLoading] = useState(false)

  const formik = useFormik({
    initialValues: {
      query: ''
    },
    onSubmit: (values, { resetForm }) => {

      async function query() {
        setLoading(true);
        try {
          const resp = await ENSInstance.withProvider(provider).getOwner(
            values.query
          )
          setLoading(false);
          setResponse(resp);
        } catch (e) {
          console.error(e)
        }
      }

      query();
    },
    validationSchema: Yup.object().shape({
      query: Yup.string()
        .min(1)
        .required('Required'),
    })
  });

  return (
    <div className="App">
      <form onSubmit={formik.handleSubmit}>
        <Container>
          <Row style={{paddingTop: 20}}>
            <Col>
              <TextArea 
                id="query"
                placeholder={"Fill in query here"}
                style={{width: '100%', minHeight: 150}}
                fill={true}
                onChange={formik.handleChange}
                // onKeyDown={(e) => {
                //   const keyCode = e.keyCode || e.which;
                //   if (keyCode === 9) {
                //     e.preventDefault();
                //     const { selectionStart, selectionEnd } = e.target;
                //     const { query } = formik.values;
                //     setFieldValue('query', 
                //       query.substring(0, selectionStart) + '    ' + query.substring(selectionEnd, query.length)
                //     );
                //   }
                // }}
                value={formik.values.query}
              />
            </Col>
          </Row>
          <Row>
            <Col>
              {loading && <Spinner />}
              <Button 
                intent="primary"
                icon="play"
                type="submit"
              >
                Execute
              </Button>
            </Col>
          </Row>
          <Row>
            <Col>
              <pre>
                {JSON.stringify(response, null, 2)}
              </pre>
            </Col>
          </Row>
        </Container>
      </form>
    </div>
  );
}

function App() {
  if(!v.ethereum) {
    return <div>Not logged in into Metamask</div>
  } else {
    return <ENSForm />
  }
}

export default App;
